import { NextRequest, NextResponse } from "next/server"
import { requireCharity } from "@/app/api/_auth"
import { wasteLogs, pickupRequests } from "@/app/api/_data"
import type { WasteLog } from "@/app/api/_types"

type AvailableItem = Pick<
  WasteLog,
  "id" | "cafeId" | "itemName" | "type" | "quantity" | "expiryDate" | "status"
>

// GET /api/charity/available
// Lists edible waste items assigned to charity that are still pending and available for donation.
// Only shows items that:
// - Are assigned to "charity"
// - Have status "pending"
// - Are not already requested (no pickup request exists for this waste)
export async function GET(req: NextRequest) {
  const { user, response } = requireCharity(req)
  if (!user || response) return response!

  // Get all waste IDs that already have pickup requests
  const requestedWasteIds = new Set(
    pickupRequests.map((p) => p.wasteId)
  )

  const items: AvailableItem[] = wasteLogs
    .filter(
      (w) =>
        w.type === "edible" &&
        w.status === "pending" &&
        w.assignedTo === "charity" &&
        !requestedWasteIds.has(w.id) // Don't show items that already have requests
    )
    .map((w) => ({
      id: w.id,
      cafeId: w.cafeId,
      itemName: w.itemName,
      type: w.type,
      quantity: w.quantity,
      expiryDate: w.expiryDate,
      status: w.status,
    }))

  return NextResponse.json(items)
}

